import { Container } from 'reactstrap';
import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';

import List from './List';
import Modify from './Modify';
const host = window.location.origin.indexOf('localhost') >= 0 ? '' : 'https://kalininavaleri.github.io/todo-app-react';
console.log('---host',host);

class App extends Component {
  render() {


    return (
        <Container>
        <h1 className="app__title">todos</h1>
          <Switch key="content">
            <Route path={`${window.location.origin}/`} exact component={List} />
            <Route path={`${window.location.origin}/modify/new`} exact component={Modify} />
            <Route path={`${window.location.origin}/modify/edit/:id`} exact component={Modify} />
          </Switch>
        </Container>
    );
  }
}

export default App;


